import axios from "axios";
import store from '../../store/index'
export const testimonialController = {
    getItems: async function () {
        var mainUrl = store.state.mainUrl;
        var data = [];
        await axios.get(mainUrl + '/testimonial').then((res) => {
            console.log(res.data.data);
            data = res.data.data
        });
        return data;
    },


    addTestimonial: async function (file, testimonial) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("title[en]", testimonial.title.en);
        formData.append("title[ar]", testimonial.title.ar);
        formData.append("title[tr]", testimonial.title.tr);
        formData.append("description[en]", testimonial.description.en);
        formData.append("description[ar]", testimonial.description.ar);
        formData.append("description[tr]", testimonial.description.tr);
        await axios.request({
            method: "POST",
            url: mainUrl + "/testimonial",
            data: formData,
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                if (res.data.state) {
                    state = true;
                    this.ShowAlert('secondary', 'Don Add Success ')
                    console.log(res.data);
                } else {
                    this.ShowAlert('red', res.data.error)
                }
            }
        }).catch(error => {
            this.ShowAlert('red', 'An error ' + error)
        });
        return state;
    },

    UpdateTestimonial: async function (file, testimonial) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("title[en]", testimonial.title.en);
        formData.append("title[ar]", testimonial.title.ar);
        formData.append("title[tr]", testimonial.title.tr);
        formData.append("description[en]", testimonial.description.en);
        formData.append("description[ar]", testimonial.description.ar);
        formData.append("description[tr]", testimonial.description.tr);
        await axios.request({
            method: "POST",
            url: mainUrl + "/testimonial/" + testimonial.id,
            data: formData,
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                if (res.data.state) {
                    state = true;
                    this.ShowAlert('secondary', 'Don Update Success ')
                    console.log(res.data);
                } else {
                    this.ShowAlert('red', res.data.error)
                }
            }
        }).catch(error => {
            this.ShowAlert('red', 'An error ' + error)
        });
        return state;
    },
    deleteItem: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/testimonial/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ' + error);
            }
            );
        return state;

    },

    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }
};